module.exports = {
  name: 'Value',
  structure: {
    children: [[]]
  },
  parse: function parse() {
    var start = this.scanner.tokenStart;
    var children = this.readSequence(this.scope.Value);
    return {
      type: 'Value',
      loc: this.getLocation(start, this.scanner.tokenStart),
      children: children
    };
  },
  generate: function generate(node) {
    this.children(node);
  }
};